interface GImg {
  id: string;
  pathSmall: string;
  pathBig: string;
}

interface GObj {
  id: string;
  title: string;
  download_all?: string;
  getImgs: () => GImg[];
  getImgs2: () => GImg[];
}

interface GPage {
  id: string;
  title: string;
  gallery_obs: GObj[];
}

function pad(num: number, size: number) {
  let s = num + "";
  while (s.length < size) s = "0" + s;
  return s;
}
const getImgs1 = function () {
  const imgs = [];
  for (let i = 1; i <= 115; i++) {
    const si = pad(i, 3);
    imgs.push({
      id: `i${si}`,
      pathSmall: `./2025-07-26t/${si}.jpg`,
      pathBig: `./2025-07-26b/${si}.jpg`,
    } as GImg);
  }
  return imgs;
};
const page_obs = [] as GPage[];
page_obs.push({
  id: "2025-07-26",
  title: "2025-07-26",
  gallery_obs: [
    {
      id: "2025-07-2",
      title: "",
      getImgs: () => getImgs1(),
      download_all:
        "https://drive.google.com/file/d/18iNQNTpzqLh5_wwC-b0t-yROSvT7G6IK",
    } as GObj,
  ],
} as GPage);
export { GImg, GObj, GPage, page_obs };
